.informations-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100vw;
    height: 100%;
    background: linear-gradient(135deg, #5DD1CD, #116B68);
    color: #FFFFFF;
}
@media only screen and (min-width: 1200px) {
    .informations-container {
        height: 100vh;
    }
}
.back-arrow {
    font-size: 3rem;
}
.informations-container > a {
    position: absolute !important;
    top: 3px;
    left: 3px;
    color: #FFFFFF;
}
.my-photo {
    margin-top: 100px;
    width: 200px;
    height: auto;
    border-radius: 50%;
}
h1 {
    margin-top: 20px;
}
p {
    padding: 0px 10px;
    text-align: justify;
}
.mini-logo {
    vertical-align: middle;
    width: 20px;
    height: auto;
}
svg {
    vertical-align: middle;
    margin-left: 5px;
    margin-right: 5px;
    width: 100px;
    height: auto !important;
}